@font-face {
  font-family: 'Comic';
  src: url(./fonts/comic.ttf) format("truetype");
  font-weight: normal;
  font-style: italic; }

@media (min-width: 100px) {
  body {
    margin: 0;
    padding: 0;
    background-color: #f8f8f8; }
  .content-row__readMore {
    margin-top: 20px !important;
    margin: 0; }
  #content {
    font-family: 'TTLakesL';
    margin-left: 15px;
    font-size: 14.8px;
    line-height: 20px;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.5s linear; }
  #content.show {
    opacity: 1;
    max-height: 170px; }
  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(190, 190, 190, 0.7);
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 10; }
  .burger-menu.visible ~ .overlay {
    visibility: visible;
    opacity: 1; }
  div.content-row {
    margin-top: 15px;
    flex-grow: 1;
    padding: 0px !important; }
  .header {
    background-color: white;
    height: 88px;
    display: flex; }
  .header__menu {
    display: flex;
    width: 100%; }
  .header__menu-img {
    padding: 10px; }
  .header__link {
    align-self: center;
    width: 40px;
    height: 40px;
    background-color: #ff3e79;
    border-radius: 50px;
    margin-left: 15px; }
  .logo {
    all: unset;
    align-self: center;
    margin-left: 15px;
    margin-right: 25px !important; }
  .header__divider.divider {
    all: unset;
    border-radius: 20px;
    width: 2px;
    height: 32px;
    background-color: #eaeaea;
    align-self: center; }
  .title {
    display: flex;
    align-self: center; }
  .title__highlight {
    width: 4px;
    height: 40px;
    background-color: #41f6d7;
    align-self: center;
    margin-right: 15px;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px; }
  .repair {
    margin-left: auto; }
  .search {
    margin-right: 15px; }
  .article-more {
    display: none; }
  .title__text {
    color: #1b1c21;
    font-family: 'TTLakes';
    align-self: center;
    font-size: 28px; }
  .navigation__nav-list {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    white-space: nowrap;
    height: 40px;
    margin-top: -5px;
    padding-top: 5px;
    padding-left: 25px; }
  img.arrows-img {
    margin: 0; }
  .navigation__nav-link {
    list-style-type: none;
    padding: 5px 15px; }
  .link-navigation {
    text-decoration: none;
    color: #7e7e82;
    font-family: 'TTLakesM'; }
  .link-navigation.active {
    padding: 5px 15px;
    margin-left: -20px !important;
    margin-right: -20px !important;
    background-color: white;
    content: '';
    border: 2px solid #b8ffec;
    border-radius: 5px;
    text-decoration: none;
    text-decoration: none;
    color: #7e7e82;
    font-family: 'TTLakesM'; }
  .read-more_title-read {
    margin-left: 5px; }
  .content-row__content {
    font-family: 'TTLakesL';
    font-size: 15px;
    margin-top: 25px;
    line-height: 20px;
    color: #1b1c21; }
  .image-cps {
    margin-top: 40px; }
  .company-img {
    position: relative;
    left: 50%;
    right: 50%;
    margin-left: -50vw;
    width: 100vw; }
  .arrows-img {
    margin-top: 13px;
    align-self: flex-start;
    margin-right: 3px;
    margin-left: 0 !important; }
  .content-row__bt-ReadMore {
    display: flex;
    margin-top: 10px;
    margin-left: 20px;
    align-items: center;
    margin-bottom: 15px; }
  .rotate {
    transform: rotate(180deg); }
  .call,
  .chat,
  .contact {
    display: none; }
  .article-longmore {
    display: none; } }

@media (min-width: 768px) {
  body {
    margin: 0;
    padding: 0;
    background-color: #f8f8f8; }
  article.article-more {
    display: block;
    margin-top: 10px;
    margin-bottom: 7px;
    width: 300px; }
  img.content-row__company-img {
    left: 380px;
    border-radius: 5px;
    top: 225px;
    width: 360px;
    height: 198px; }
  .header__divider.divider-two {
    border-radius: 20px;
    display: flex;
    width: 2px;
    height: 32px;
    align-self: center;
    margin-left: 15px;
    background-color: #eaeaea; }
  div.header__divider.divider {
    width: 2px;
    order: -2;
    margin-left: 25px;
    border-radius: 20px; }
  .header__link.logo {
    order: -1; }
  .header__link.list {
    order: -3; }
  .header__link.call,
  .header__link.chat,
  .header__link.contact {
    display: flex; }
  .header__link.call {
    margin-left: auto; }
  .header__link.repair {
    margin-left: 15px; }
  .header__link.search {
    margin-right: 30px; }
  img.arrows-img {
    margin-top: 5px; } }

.burger-menu {
  position: fixed;
  z-index: 11;
  height: 100%;
  width: 250px;
  top: 0;
  left: -250px;
  box-shadow: 5px 0 20px rgba(0, 0, 0, 0.3);
  visibility: hidden;
  box-shadow: 5px 0 20px rgba(0, 0, 0, 0.3);
  background-color: #f8f8f8; }

.burger-menu.visible {
  transform: translateX(250px);
  visibility: visible; }

.toggle-menu {
  margin: 20px; }

section.burger-menu {
  transition: 0.5s !important; }

.header-burger {
  display: flex;
  padding-top: 20px;
  margin-left: 10px;
  align-items: center; }

.header__close {
  margin-left: 4px; }

.header__close-bg,
.header__search-bg {
  background-repeat: no-repeat;
  background-position: center;
  background-color: #ff3e79;
  width: 40px;
  height: 40px;
  border-radius: 50%; }

.header__close-bg {
  background-image: url(./static/close.svg); }

.header__search-bg {
  background-image: url(./static/search.svg); }

.logo {
  margin-left: 15px;
  margin-right: 45px; }

a {
  padding: 0;
  margin: 0; }

ul {
  padding: 0;
  margin: 50px 0 0 0; }

.list-li__a {
  font-family: TTLakesM;
  color: #1b1c21;
  text-decoration: none;
  font-size: 16px;
  line-height: 24px;
  margin-left: 20px; }

.list-li__li-link {
  margin-bottom: 20px;
  padding-top: 5px !important; }

div.divider {
  position: absolute;
  width: 4px;
  height: 32px;
  background-color: #41f6d7;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px; }

.active {
  color: #7e7e82; }

a.list-li__a {
  opacity: 1;
  transition: opacity 0.5s ease; }

a.list-li__a:hover:not(.active) {
  opacity: 0.6; }

.phone-bg {
  background-image: url(./static/phone.svg);
  margin-left: 0;
  background-repeat: no-repeat;
  background-position: center; }

.comment-bg {
  background-image: url(./static/comment.svg);
  background-size: 25px;
  background-repeat: no-repeat;
  background-position: center; }

.down-menu {
  margin-left: 15px;
  display: flex;
  gap: 15px;
  position: absolute;
  left: 0;
  bottom: 170px; }

a.down-menu__link {
  display: block;
  background-color: #ff3e79;
  width: 40px;
  height: 40px;
  border-radius: 50%; }

.man-bg {
  background-image: url(./static/man.svg);
  background-repeat: no-repeat;
  background-position: center; }

.email {
  text-decoration: none;
  color: #1b1c21;
  font-family: TTLakesM;
  font-size: 16px; }

.tel {
  text-decoration: none;
  color: #1b1c21;
  font-family: TTLakes;
  font-size: 24px; }

div.down-info {
  position: absolute;
  bottom: 0;
  flex-direction: column;
  gap: 10px;
  display: flex;
  margin-left: 20px;
  margin-bottom: 25px; }

.down-info__change-lang {
  display: flex;
  gap: 5px;
  margin-top: 30px; }

button {
  background-color: #f8f8f8;
  border: 0;
  font-family: TTLakesM;
  font-size: 16px;
  transition: 0.3s;
  padding: 0;
  color: #1b1c21; }

.button:hover:not(.lang-active) {
  opacity: 0.6; }

.down-info_chanlg {
  list-style: none; }

.lang-active {
  color: #7e7e82 !important; }

.down-menu__ul-DMenu {
  display: flex;
  gap: 15px;
  margin-left: 5px; }

.down-menu__li-DMenu {
  list-style: none; }

@media (max-height: 720px) {
  .list-li__li-link {
    margin-bottom: 10px; }
  .tel {
    margin-bottom: 0; }
  ul.list-li {
    margin-top: 30px; }
  .down-menu {
    bottom: 135px; }
  .down-info__change-lang {
    margin-top: 3px; } }

@media (max-height: 600px) {
  .list-li__li-link {
    margin-bottom: 5px; }
  ul.list-li {
    margin-top: 15px !important; }
  .tel {
    margin-bottom: 0; } }

.services__title,
.services__title-two,
.services__title-thee {
  display: flex;
  font-family: TTLakesM;
  border-top: 2px solid #D9FFF5;
  font-size: 16px;
  height: 52px;
  align-items: center;
  line-height: 24px;
  color: #7e7e82;
  text-transform: uppercase;
  background-color: white;
  padding: 10px;
  margin: 0;
  padding-left: 15px; }

.services__title-two {
  margin-top: 15px; }

.swiper {
  margin-top: 15px;
  width: 100%;
  height: 115px; }

.list_li,
.list_li-two {
  list-style: none; }

.swiper-slide {
  height: 72px;
  background-color: white;
  border: 2px solid #eaeaea;
  border-radius: 7px;
  display: flex !important;
  justify-content: space-between;
  align-items: center;
  padding: 15px; }

.swiper-wrapper {
  margin-left: 15px !important; }

.list-equipment,
.list-equipment-two {
  margin-top: 30px;
  display: flex;
  flex-wrap: wrap;
  padding-left: 25px;
  gap: 25px;
  row-gap: 20px; }

button.list-equipment__item,
.list-equipment__item-two {
  height: 72px;
  width: 222px;
  background-color: white;
  border: 2px solid #eaeaea;
  border-radius: 7px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

button.hidden {
  display: none; }

button.read-more,
button.read-more-two {
  all: unset;
  margin-left: 25px;
  margin-top: 20px;
  display: flex;
  align-items: center;
  font-family: TTLakesM;
  line-height: 24px;
  font-size: 16px;
  flex-shrink: 0 !important; }

.rotate-img {
  transform: rotate(180deg); }

.swiper-pagination .swiper-pagination-bullet {
  background: #7e7e82; }

@media (min-width: 1120px) {
  .list-equipment__item:nth-child(7),
  .list-equipment__item:nth-child(8) {
    display: flex; }
  button.list-equipment__item {
    width: 240px; } }

@media (max-width: 767px) {
  ul.list-equipment {
    display: none; }
  button.read-more {
    display: none; } }

@media (min-width: 767px) {
  div.swiper,
  div.swiper-pagination {
    display: none; }
  .go {
    margin-right: 7px; }
  .picture {
    margin-left: 5px; }
  .services__title {
    padding-left: 26px; } }

.two {
  height: 150px;
  align-items: flex-start;
  justify-content: flex-start; }

.swiper-two {
  margin-top: 15px;
  width: 100%;
  height: 190px; }

.go-two {
  margin-left: auto;
  margin-top: auto; }

@font-face {
  font-family: 'TTLakes';
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype"); }

@font-face {
  font-family: 'TTLakesM';
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype"); }

@font-face {
  font-family: 'TTLakesL';
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype"); }
